#sidebar {
  overflow: auto;
  height: 100vh;
  border-radius: 50px;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
/* #sidebar */
::-webkit-scrollbar-track {
  background: white;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #000054;
  border-radius: 3px;
  padding: 50px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #000054;
}
